import Container from '../container';
import Button from '../button';
import SectionHeader from '../section-header';
import SectionHeaderLeft from '../section-header-left';
import Image from '../image';
import CompanySlider from '../home/company-slider';
import { Row, Col, ListGroupItem, ListGroup } from 'reactstrap';
export default () => (
  <Container wide role="region" aria-labelledby="features">
    <Container center padding>
      <SectionHeader
        id="des"
        title="Data Overview"
        description="Our dataset covers the National University of Singapore(NUS) kent ridge campus and is divided into the following areas:"
      />
      <div className="row">
        <div className="column">
          <div className={`photo`}>
            <img
              // src="../../static/images/map.jpg"
              src="../../static/images/map.png"
              alt="map"
              height="924"
              width="1000"
            />
          </div>
        </div>
      </div>
      <style jsx>{`
        .column {
          text-align: center;
          max-width: 300px;
        }
        .photo {
          width: 100%;
          height: 100%;
          text-align: center;
          display: fix;
          align-items: center;
          justify-content: center;
        }
        // CSS only media query for tablet
        @media screen and (max-width: 960px) {
          .row {
            flex-direction: column;
            margin: -1.5rem 0;
          }
          .column {
            width: 100%;
            padding: 1.5rem 0;
            text-align: center;
            max-width: 150px;
          }
        }
      `}</style>
    </Container>
    <Container />
    <Container left>
      <div>
        <p>*Datasets of Utown and Faculty of Science will be released later.</p>
      </div>
      <div>
        <p>
          Each structure or object has been annotated in dataset with their tags
          and classes. A tag is a sole attribute for an individual object
          containing all points belong to it. A class is assigned to each object
          according to its semanteme and function. Figures below indicate the
          example of tags and classes of building points: in the above figure,
          all classes are demonstrated in different colours.
        </p>
      </div>
      <style jsx>{`
        .column {
          text-align: left;
          max-width: 400px;
        }
        .photo {
          width: 100%;
          height: 100%;
          text-align: center;
          display: fix;
          align-items: center;
          justify-content: center;
        }
        // CSS only media query for tablet
        @media screen and (max-width: 960px) {
          .row {
            flex-direction: column;
            margin: -1.5rem 0;
          }
          .column {
            width: 100%;
            padding: 1.5rem 0;
            text-align: center;
            max-width: 150px;
          }
        }
      `}</style>
    </Container>

    <Container center>
      <SectionHeader id="label" title="Labels in the dataset" />
      <div className="row">
        <div className="column">
          <div className={`photo`}>
            <img
              src="../../static/images/mlb_resize.png"
              alt="mlb"
              height="720"
              width="1000"
            />
          </div>
        </div>
      </div>

      <div className="row" center>
        <div className="column">
          <div className={`photo`}>
            <img src="../../static/images/label/normal.gif" alt="" />
          </div>
          <p>Original</p>
        </div>
        <div className="column">
          <div className={`photo`}>
            <img src="../../static/images/label/full.gif" alt="" />
          </div>
          <p>Fully labeled</p>
        </div>
        <div className="column">
          <div className={`photo`}>
            <img src="../../static/images/label/withouttree.gif" alt="" />
          </div>
          <p>Without trees</p>
        </div>
      </div>

      <style jsx>{`
        .column {
          text-align: center;
          max-width: 450px;
        }
        .photo {
          width: 100%;
          height: 100%;
          text-align: center;
          display: fix;
          align-items: center;
          justify-content: center;
        }
        // CSS only media query for tablet
        @media screen and (max-width: 960px) {
          .row {
            flex-direction: column;
            margin: -1.5rem 0;
          }
          .column {
            width: 100%;
            padding: 1.5rem 0;
            text-align: center;
            max-width: 150px;
          }
        }
      `}</style>
    </Container>
    <Container left>
      <SectionHeader
        id="paper"
        title="Detailed information"
        description="Please refer to our paper for data collection, area division, statistics, baselines and models."
      />
      <div className="row">
        <div className="column">
          <Button href="https://arxiv.org/pdf/2008.04968.pdf">MM Paper</Button>
        </div>
        <div className="column">
          <Button href="https://nusdataset.s3.ap-southeast-1.amazonaws.com/3d/Campus3D_supp.pdf">Supplementary Materials</Button>
        </div>
        <div className="column">
          <Button href="https://dl.acm.org/doi/pdf/10.1145/3474085.3477934">
          Reproducibility Paper
                  </Button>
        </div>
      </div>
      <style jsx>{`
        .column {
          text-align: left;
        }
        // CSS only media query for tablet
        @media screen and (max-width: 960px) {
          .row {
            flex-direction: column;
            margin: -1.5rem 0;
          }
          .column {
            width: 100%;
            padding: 1.5rem 0;
            text-align: center;
            max-width: 350px;
          }
        }
      `}</style>
      <div className="row">
        <p />
      </div>
      <div className="row">
        <p />
      </div>
      <style jsx>{`
        .column {
          text-align: left;
          max-width: 400px;
        }
        .photo {
          width: 100%;
          height: 100%;
          text-align: center;
          display: fix;
          align-items: center;
          justify-content: center;
        }
        // CSS only media query for tablet
        @media screen and (max-width: 960px) {
          .row {
            flex-direction: column;
            margin: -1.5rem 0;
          }
          .column {
            width: 100%;
            padding: 1.5rem 0;
            text-align: center;
            max-width: 150px;
          }
        }
      `}</style>
    </Container>

    <Container />
  </Container>
);
